import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import chuangyekechengList from '@/views/pages/chuangyekecheng/list'
import chuangyekechengDetail from '@/views/pages/chuangyekecheng/formModel'
import chuangyekechengAdd from '@/views/pages/chuangyekecheng/formAdd'
import forumList from '@/views/pages/forum/list'
import chuangyexiangmuList from '@/views/pages/chuangyexiangmu/list'
import chuangyexiangmuDetail from '@/views/pages/chuangyexiangmu/formModel'
import chuangyexiangmuAdd from '@/views/pages/chuangyexiangmu/formAdd'
import xiangmuleixingList from '@/views/pages/xiangmuleixing/list'
import xiangmuleixingDetail from '@/views/pages/xiangmuleixing/formModel'
import xiangmuleixingAdd from '@/views/pages/xiangmuleixing/formAdd'
import chuangyeziyuanList from '@/views/pages/chuangyeziyuan/list'
import chuangyeziyuanDetail from '@/views/pages/chuangyeziyuan/formModel'
import chuangyeziyuanAdd from '@/views/pages/chuangyeziyuan/formAdd'
import kechengleixingList from '@/views/pages/kechengleixing/list'
import kechengleixingDetail from '@/views/pages/kechengleixing/formModel'
import kechengleixingAdd from '@/views/pages/kechengleixing/formAdd'
import storeupList from '@/views/pages/storeup/list'
import yuyuexinxiList from '@/views/pages/yuyuexinxi/list'
import yuyuexinxiDetail from '@/views/pages/yuyuexinxi/formModel'
import yuyuexinxiAdd from '@/views/pages/yuyuexinxi/formAdd'
import exampaperList from '@/views/pages/exam/exampaperList'
import examination from '@/views/pages/exam/examination'
import examrecordList from '@/views/pages/exam/examrecordList'
import examfailrecord from '@/views/pages/exam/examfailrecord'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'chuangyekechengList',
			component: chuangyekechengList
		}, {
			path: 'chuangyekechengDetail',
			component: chuangyekechengDetail
		}, {
			path: 'chuangyekechengAdd',
			component: chuangyekechengAdd
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'chuangyexiangmuList',
			component: chuangyexiangmuList
		}, {
			path: 'chuangyexiangmuDetail',
			component: chuangyexiangmuDetail
		}, {
			path: 'chuangyexiangmuAdd',
			component: chuangyexiangmuAdd
		}
		, {
			path: 'xiangmuleixingList',
			component: xiangmuleixingList
		}, {
			path: 'xiangmuleixingDetail',
			component: xiangmuleixingDetail
		}, {
			path: 'xiangmuleixingAdd',
			component: xiangmuleixingAdd
		}
		, {
			path: 'chuangyeziyuanList',
			component: chuangyeziyuanList
		}, {
			path: 'chuangyeziyuanDetail',
			component: chuangyeziyuanDetail
		}, {
			path: 'chuangyeziyuanAdd',
			component: chuangyeziyuanAdd
		}
		, {
			path: 'kechengleixingList',
			component: kechengleixingList
		}, {
			path: 'kechengleixingDetail',
			component: kechengleixingDetail
		}, {
			path: 'kechengleixingAdd',
			component: kechengleixingAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'yuyuexinxiList',
			component: yuyuexinxiList
		}, {
			path: 'yuyuexinxiDetail',
			component: yuyuexinxiDetail
		}, {
			path: 'yuyuexinxiAdd',
			component: yuyuexinxiAdd
		}
		, {
			path: 'exampaperList',
			component: exampaperList
		}, {
			path: 'examrecordList',
			component: examrecordList
		}, {
			path: 'examfailrecord',
			component: examfailrecord
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
	, {
		path: '/examination',
		component: examination
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
